import React from "react"

// import { useAppSelector } from "../hooks/redux-hooks";
// import {UserModel} from "../models/redux-models";

import { Navigate, Outlet } from "react-router-dom"
import { useAtom } from 'jotai'
import { Authenticate } from "../store/atom"

//protected Route state
type ProtectedRouteType = {
	roleRequired?: "ADMIN" | "USER"
	topath:Partial<Location> | string
}

export const ProtectedRoutes = (props: ProtectedRouteType) => {
	const  [LoginedUser,]  = useAtom(Authenticate);
//	const stateuser: UserModel=useAppSelector(state=>state.user);
	//if the role required is there or not
	if (props.roleRequired) {
		return LoginedUser.authenticated ? (
			props.roleRequired === LoginedUser.role ? (
				<Outlet />
			) : (
				<Navigate to="/denied" />
			)
		) : (
			<Navigate to={props.topath} />
		)
	} else {
		return LoginedUser.authenticated ? <Outlet /> : <Navigate to={props.topath} />
	}
}

