import React, { useState } from 'react';
import { useInterval } from 'react-use'
import { useNavigate } from "react-router-dom"
import { useAtom } from 'jotai'
import { useAtomDevtools } from 'jotai/devtools'
import { loadableAsyncUser, Authenticate, PasswordUser } from "../store/atom"

// const userlogin: UserModel = { username: "eve.holt@reqres.in", password: "cityslicka" };


const Login = () => {
	const value = useAtom(loadableAsyncUser);
	const [LoginedUser, setLoginedUser] = useAtom(Authenticate);
	useAtomDevtools(Authenticate);
	const [first, setfirst] = useState(true);
	const [pwun, setPasswordusername] = useAtom(PasswordUser);
	console.log("Inlogin:", LoginedUser);
	const navigate = useNavigate();
	var timer = 500;
	useInterval(Intervaltimer, timer);
	function Intervaltimer() {
		if ((first === false) && ((value[0].state) === 'hasData')) { //loading  hasError
			setLoginedUser((draft) => {         //
				draft.authenticated = true;
				draft.role = "ADMIN"
			});
			console.log("login-button:", LoginedUser);
			setPasswordusername((draft) => {    //clear password and username
				draft.username = "";
				draft.password = ""
			});
			navigate("/dashboard");
		}
	}
	const login = () => {
		console.log("login pwun:",pwun);		
		setPasswordusername((draft) => {     //get username and password
			draft.username = "eve.holt@reqres.in";
			draft.password = "cityslicka"
		});
		setfirst(false);     //second no first
		console.log("login-button-first2:", first);
	}
	return (
		<div className="login">
			<h2>Welcome to login page! </h2>
			<p>Please loging to continue</p>
			<button onClick={login}> Login {(value[0].state)}</button>
		</div>
	)
}

export default Login
