import React from "react"
import { Link, useLocation, useNavigate } from "react-router-dom"
// import { UserModel } from "../models/redux-models";
// import { useAppDispatch, useAppSelector } from "../hooks/redux-hooks";
// import { actionLogout } from '../store/user-actions';
import { navigationItems } from "../config"
import { useAtom } from 'jotai'
import { Authenticate } from "../store/atom"
import { useAtomDevtools } from 'jotai/devtools'





const Sidebar = () => {
	const [LoginedUser, setLoginedUser] = useAtom(Authenticate);
	useAtomDevtools(Authenticate);
//	const dispatch = useAppDispatch();
//	const thisuser: UserModel = useAppSelector(state => state.user);
//	console.log("thisuser:", thisuser);

	const user = LoginedUser.authenticated;
	const location = useLocation();

	const navigation = useNavigate();
	console.log("InSidebar:", LoginedUser);

	const logout = () => {
        setLoginedUser((draft)=>{
			draft.authenticated = !true;
			draft.role =""
		});
		console.log("Sidebar-logoutbutton:", LoginedUser);
	//	dispatch(actionLogout(thisuser));
		navigation("/login")
	}

	return (
		<div className="sidebar">
			<div className="sidebar__items">
				{user && (
					<>
						{navigationItems.sidebar.map((item) => (
							<Link
								key={item.text}
								to={item.to}
								className={
									location.pathname.includes(item.to) ? "sidebar_active" : ""
								}
								>
								{item.name}
							</Link>
						))}
						{location.pathname !== "/login" && (
							<button onClick={logout}>logout</button>
						)}
					</>
				)}
				{!user && (
					<Link
						to="/login"
						className={location.pathname === "/login" ? "sidebar_active" : ""}>
						Login
					</Link>
				)}
			</div>
		</div>
	)
}

export default Sidebar
