import React from "react"
import { useAtom } from 'jotai'
import { Authenticate } from "../store/atom"



// import { useAppSelector } from "../hooks/redux-hooks";
// import { UserModel } from "../models/redux-models";

type Props = {
	roleRequired: "ADMIN" | "USER"
	message?: string
	children?: React.ReactNode
}


const WithPermission = (props: Props) => {
	const [LoginedUser,] = useAtom(Authenticate);
	//const thisuser: UserModel = useAppSelector(state => state.user);
	const { roleRequired, message, children } = props
	const role = LoginedUser.role;
	return (
		<>{roleRequired === role ? children : <h3>{message ? message : ""}</h3>}</>
	)
}

export default WithPermission
