import React from 'react';
import axios from 'axios';
import { atom } from 'jotai'
import { loadable } from "jotai/utils"
import { atomWithImmer } from 'jotai/immer'
import { Login,Password} from "./types";


//const urlbase = window.location.href;
const BASEURL={baseURL:'https://reqres.in/api/'};

export const Authenticate = atomWithImmer<Login>({authenticated:false,role:""});
export const PasswordUser = atomWithImmer<Password>({username:"",password:""});
const AsyncUser = atom(async (get) => {
    const pdum = get(PasswordUser);
    console.log("atom-pdun",pdum);
    const response = await axios.create(BASEURL).post('/login', {username:pdum.username,password:pdum.password})
    const data = await response.data;
    console.log("response",response);
    return data;
});
export const loadableAsyncUser = loadable(AsyncUser);
